import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
import { BtnAddNew } from 'components/common/button';
import { TableData, ColEditDelete } from 'components/common';
import {
  getGraduateClassification,
  deleteGraduateClassification,
} from 'services/GraduateClassification';
import { Card, Row, Col, message } from 'antd';
import { Link } from 'react-router-dom';
import routeConstant from 'routes/route/routeConstant';

const ROUTER_FORM_DATA =
  routeConstant.DANH_MUC__DIEM_XEP_LOAI__XEP_HANG_NAM_TOT_NGHIEP__NHAP_DU_LIEU;
class RatingGraduatePage extends Component {
  state = {};

  handleDelete = async id => {
    await deleteGraduateClassification(id);
    message.success('Xa thnh cng');
    this.setState({
      reload: this.state.reload + 1,
    });
  };

  render() {
    const { reload } = this.state;
    const columns = [
      {
        title: 'Xp hng',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Tn ting anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'T im',
        dataIndex: 'fromGrade',
        key: 'fromGrade',
      },
      {
        title: 'n im',
        dataIndex: 'toGrade',
        key: 'toGrade',
      },
      {
        title: 'T im thang 10',
        dataIndex: 'fromGradeIn10',
        key: 'fromGradeIn10',
      },
      {
        title: 'n im thang 10',
        dataIndex: 'toGradeIn10',
        key: 'toGradeIn10',
      },
      {
        title: 'Thao tc',
        dataIndex: 'operation',
        render: (text, record) => (
          <ColEditDelete
            linkEdit={{
              to: {
                pathname: ROUTER_FORM_DATA,
                search: `?id=${record.id}`,
              },
            }}
            onConfirmDelete={() => this.handleDelete(record.id)}
          />
        ),
      },
    ];
    return (
      <Card title="Danh mc xp hng tt nghip">
        <Row>
          <Col span={24}>
            <Link to={ROUTER_FORM_DATA}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <TableData
              reload={reload}
              service={getGraduateClassification}
              columns={columns}
              rowKey={record => record.label + record.id}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <RatingGraduatePage />
  </Page>
);



